<template>
  <div class="row">
    <div class="col-12">
      <h4>Pedidos de Reserva</h4>
      <card>
        <div slot="raw-content" class="table-responsive">
          <table class="table table-striped">
            <thead>
              <tr>
                <th>Id</th>
                <th>Atividade</th>
                <th>Cliente</th>
                <th>Data</th>
                <th>Horário</th>
                <th>Nº Pessoas</th>
                <th>Responsável</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in tabelaDados" :key="index">
                <td>{{ item.id }}</td>
                <td>{{ getNomeAtividade(item.idAtividade) }}</td>
                <td>{{ getNomeCliente(item.id_cliente) }}</td>
                <td>{{ item.dataReserva }}</td>
                <td>{{ item.horaInicio }}:{{ item.horaFim }}</td>
                <td>{{ item.numPessoas }}</td>
                <td>{{ getNomeFuncionario(item.responsavel) }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tabelaDados: []
    };
  },
  mounted() {
    let dadosLocalStorage = JSON.parse(localStorage.getItem('reservas'));
    console.log(dadosLocalStorage)
    if (dadosLocalStorage) {
      this.tabelaDados = dadosLocalStorage;
    }
  },
  methods: {
    getNomeAtividade(id){
      let atividades = JSON.parse(localStorage.getItem('atividades'));
      let atividade = atividades.find(a => a.id === id);
      console.log(atividade)
      return atividade.nome;
    },
    getNomeCliente(id){
      let clientes = JSON.parse(localStorage.getItem('utilizadores'));
      let cliente = clientes.find(a => a.id === id);
      console.log(atividade)
      return cliente.nome;
    },
    getNomeFuncionário(id){
      let clientes = JSON.parse(localStorage.getItem('funcionarios'));
      let cliente = clientes.find(a => a.id === id);
      console.log(atividade)
      return cliente.nome;
    }
  },
};
</script>
